@import url("Theme.css");
#experience {
  display: flex;
  height: auto;
  width: 100%;
  flex-direction: column;
  color: rgb(255, 255, 255);
  padding-top: 5rem;
  align-items: center;
  padding-bottom: 3rem;
  overflow-y: overlay;
}
#experience::-webkit-scrollbar {
  display: none;
}
#experience p,
h2 {
  margin-block-end: 0;
  margin-block-start: 0;
  padding: 0.4rem;
}
.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  padding-top: 4rem;
}
.experience__container > div {
  /* background: var(-color-bg-variant); */
  padding: 2.4rem 5rem;
  padding-bottom: 4rem;
  /* padding-left: 2rem;
  padding-right: 2rem; */
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.experience__container > div:hover {
  border: 1px solid white;
}

.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: white;
}
.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
  column-gap: 6rem;
}
.experience__details {
  display: flex;
  gap: 1rem;
}
.experience__details-icon {
  margin-top: 1.35rem;
  color: black;
}

@media screen and (max-width: 1360px) {
  .experience__container {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 800px) {
  .experience__container > div {
    width: 90%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience__content {
    padding: 1rem;
  }
}
@media screen and (max-width: 694px) {
  .experience__content {
    column-gap: 3rem;
  }
  .experience__container {
    width: 80%;
  }
  .experience__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
}
@media screen and (max-width: 550px) {
  .experience__content {
    column-gap: 2rem;
  }
  .experience__container {
    width: 90%;
    margin: auto;
  }
  .experience__container > div {
    width: 75%;
    padding: unset;
    margin: 0 auto;
  }
}

@media screen and (max-width: 457px) {
  .experience__container > div {
    width: 90%;
    padding: 0rem;
    margin: 0 auto;
    justify-content: space-evenly;
  }
  .experience__container {
    width: 100%;
  }
  .experience__content {
    width: 90%;
    padding: 0.5rem;
  }
}
@media screen and (max-width: 300px) {
  .experience__container {
    gap: 1rem;
  }
  .experience__content {
    column-gap: 1rem;
    padding: 0.5rem;
    row-gap: 1rem;
    width: 40%;
  }
  .experience__container > div {
    width: 90%;
    padding: 1rem 0rem;
  }
  .experience__details {
    gap: 0.5rem;
  }
}
